"use client";
import React, { createContext, useContext, useEffect, useState, useCallback } from 'react';
import { Account, Category, Transaction, FixedExpenseRule } from '@/types';
import { startOfMonth, isSameMonth, getDate, setDate } from 'date-fns';
import { v4 as uuidv4 } from 'uuid';

// DATOS INICIALES (Tus listas personalizadas y datos de ejemplo)
// NOTA: He actualizado los balances iniciales basado en tu imagen para que se vea real de entrada.
const INITIAL_ACCOUNTS: Account[] = [
    { id: 'acc_dav_th', name: 'Davivienda TH', bank: 'Davivienda', owner: 'Thannia', balance: 1168800, isHidden: false, last4: '8901', colorTheme: 'from-red-700 to-red-900' },
    { id: 'acc_dav_jh', name: 'Davivienda JH', bank: 'Davivienda', owner: 'Jhonattan', balance: 520000, isHidden: false, last4: '1234', colorTheme: 'from-red-700 to-red-900' },
    { id: 'acc_bdb_jh', name: 'BDB JH', bank: 'Banco de Bogotá', owner: 'Jhonattan', balance: 4801431, isHidden: false, last4: '5678', colorTheme: 'from-blue-800 to-blue-950' },
    { id: 'acc_neq_jh', name: 'Nequi JH', bank: 'Nequi', owner: 'Jhonattan', balance: 240000, isHidden: false, last4: '3321', colorTheme: 'from-purple-600 to-pink-600' },
    { id: 'acc_neq_th', name: 'Nequi TH', bank: 'Nequi', owner: 'Thannia', balance: 9000, isHidden: false, last4: '4455', colorTheme: 'from-purple-600 to-pink-600' },
    { id: 'acc_efec', name: 'Efectivo Casa', bank: 'Efectivo', owner: 'Casa', balance: 200000, isHidden: false, colorTheme: 'from-green-700 to-emerald-900' },
    { id: 'acc_sodexo', name: 'Sodexo', bank: 'Sodexo', owner: 'Ambos', balance: 66896, isHidden: false, colorTheme: 'from-blue-400 to-blue-600' },
  ];

// Categorías basadas en tus imágenes
const INITIAL_CATEGORIES: Category[] = [
  { id: 'cat_ing_fijo', name: 'Ingreso Fijo (Salario/Aux)', type: 'income' },
  { id: 'cat_ing_extra', name: 'Ingreso Extra', type: 'income' },
  { id: 'cat_ahorro_meta', name: 'Ahorro Meta/Beneficiar', type: 'income' }, // Para mover plata a ahorros
  
  { id: 'cat_servicios', name: 'Servicios Públicos', type: 'expense', isFixedExpenseCandidate: true, subCategories: ['Enel Codensa', 'Acueducto', 'Vanti', 'Movistar + WOM', 'Claro'] },
  { id: 'cat_casita', name: 'Casita/Arriendo', type: 'expense', isFixedExpenseCandidate: true, subCategories: ['Arriendo', 'Aseo mensual'] },
  { id: 'cat_comida', name: 'Comida', type: 'expense', isFixedExpenseCandidate: true, subCategories: ['Mercado Mensual', 'Gasto Comida Diario'] },
  { id: 'cat_moto', name: 'Moto', type: 'expense', subCategories: ['Gasolina', 'Taller', 'Seguro'] },
  { id: 'cat_deudas', name: 'Pagos/Deudas', type: 'payment', isFixedExpenseCandidate: true, subCategories: ['Banco de Bogotá', 'Colsubsidio', 'Sistecredito JH', 'Sistecredito TH', 'Addi JH'] },
  { id: 'cat_varios', name: 'Gastos Varios/Ordinario', type: 'expense', subCategories: ['Hogar', 'Gatos', 'Universidad', 'Regalos', 'Otros'] },
  // Categoría especial interna para ahorros automáticos
  { id: 'cat_sys_savings', name: 'Ahorro Automático por Superávit', type: 'income' }
];


interface FinanceContextType {
  accounts: Account[];
  transactions: Transaction[];
  categories: Category[];
  fixedRules: FixedExpenseRule[];
  addTransaction: (t: Omit<Transaction, 'id'>) => void;
  updateTransaction: (t: Transaction) => void;
  deleteTransaction: (id: string) => void;
  
  // Funciones de administración
  addCategory: (c: Category) => void;
  updateCategory: (c: Category) => void;
  addAccount: (a: Account) => void;
  updateAccount: (a: Account) => void;
  addFixedRule: (r: FixedExpenseRule) => void;
  updateFixedRule: (r: FixedExpenseRule) => void;
  deleteFixedRule: (id: string) => void;
}

const FinanceContext = createContext<FinanceContextType | undefined>(undefined);

export const FinanceProvider = ({ children }: { children: React.ReactNode }) => {
  const [accounts, setAccounts] = useState<Account[]>(INITIAL_ACCOUNTS);
  const [transactions, setTransactions] = useState<Transaction[]>([]);
  const [categories, setCategories] = useState<Category[]>(INITIAL_CATEGORIES);
  const [fixedRules, setFixedRules] = useState<FixedExpenseRule[]>([]);
  const [isInitialized, setIsInitialized] = useState(false);

  // --- PERSISTENCIA (Cargar y Guardar en localStorage) ---
  useEffect(() => {
    const loadData = () => {
        const savedTx = localStorage.getItem('transactions');
        const savedAcc = localStorage.getItem('accounts');
        const savedCat = localStorage.getItem('categories');
        const savedRules = localStorage.getItem('fixedRules');
        
        if (savedTx) setTransactions(JSON.parse(savedTx));
        // IMPORTANTE: Si ya hay cuentas guardadas, USARLAS, si no, usar las INITIAL con los saldos de tu imagen.
        if (savedAcc) {
            setAccounts(JSON.parse(savedAcc));
        } else {
             // Primera vez que corre la app, asegurar que los saldos iniciales sean los correctos
             setAccounts(INITIAL_ACCOUNTS);
        }

        if (savedCat) setCategories(JSON.parse(savedCat));
        if (savedRules) setFixedRules(JSON.parse(savedRules));
        setIsInitialized(true);
    }
    loadData();
  }, []);

  useEffect(() => {
    if (!isInitialized) return; // No guardar hasta que haya cargado
    localStorage.setItem('transactions', JSON.stringify(transactions));
    localStorage.setItem('accounts', JSON.stringify(accounts));
    localStorage.setItem('categories', JSON.stringify(categories));
    localStorage.setItem('fixedRules', JSON.stringify(fixedRules));
  }, [transactions, accounts, categories, fixedRules, isInitialized]);


  // --- LÓGICA CENTRAL: Actualizar Saldos ---
  // Recalcula los saldos de las cuentas basado en TODAS las transacciones. Es más seguro que sumar/restar.
  const recalculateBalances = useCallback((currentTransactions: Transaction[], currentAccounts: Account[]) => {
    const newAccountsMap = new Map(currentAccounts.map(acc => [acc.id, { ...acc, balance: 0 }])); // Reiniciar a 0 para recalcular

    // 1. Restaurar saldos iniciales "base" (Esto es un truco porque no tenemos DB real.
    // En un sistema real, el saldo es la suma de inputs y outputs históricos.
    // Para este prototipo, usaremos los saldos de INITIAL_ACCOUNTS como punto de partida si no hay transacciones, 
    // pero esto es complejo sin una tabla de "saldos iniciales". 
    // SIMPLIFICACIÓN PARA PROTOTIPO: El saldo es simplemente la suma algebraica de todas las transacciones.
    // NOTA: Para que esto funcione bien, deberías crear transacciones de "Saldo Inicial" el primer día.
    // POR AHORA: Usaremos una lógica incremental que funciona bien para el uso diario.

  }, []);


  // --- LÓGICA DE NEGOCIO PRINCIPAL ---

  // 1. Agregar Transacción (Manual o Automática)
  const addTransaction = (t: Omit<Transaction, 'id' | 'hasGeneratedSavings'>) => {
    const newTx: Transaction = { ...t, id: uuidv4(), hasGeneratedSavings: false };
    
    // LÓGICA DE AHORRO AUTOMÁTICO: Si es un gasto fijo y pagué MENOS de lo presupuestado.
    if (newTx.type === 'expense' && newTx.isFixed && newTx.budgetedAmount && newTx.amount < newTx.budgetedAmount) {
        const savingsAmount = newTx.budgetedAmount - newTx.amount;
        
        // Crear automáticamente la transacción de ingreso al ahorro
        const savingsTx: Transaction = {
            id: uuidv4(),
            type: 'income',
            amount: savingsAmount,
            accountId: 'acc_dav_th', // ASUMIMOS que el ahorro va a Davivienda TH (puedes cambiar esto o hacerlo configurable)
            categoryId: 'cat_sys_savings',
            description: `Ahorro automático por superávit en: ${newTx.description}`,
            date: newTx.date,
            isFixed: false
        };
        setTransactions(prev => [newTx, savingsTx, ...prev]);
        
        // Actualizar saldos (Pago del gasto Y entrada del ahorro)
        updateAccountBalance(newTx.accountId, -newTx.amount);
        updateAccountBalance(savingsTx.accountId, savingsTx.amount);
        newTx.hasGeneratedSavings = true; // Marcar para no duplicar si se edita

    } else {
        // Transacción normal
        setTransactions(prev => [newTx, ...prev]);
        const multiplier = newTx.type === 'income' ? 1 : -1;
        updateAccountBalance(newTx.accountId, newTx.amount * multiplier);
    }
  };

  // Helper simple para actualizar saldos de forma incremental
  const updateAccountBalance = (accountId: string, amountChange: number) => {
    setAccounts(prevAccounts => 
        prevAccounts.map(acc => 
            acc.id === accountId ? { ...acc, balance: acc.balance + amountChange } : acc
        )
    );
  };

  // 2. Función para generar gastos fijos al inicio del mes
  const generateMonthlyFixedExpenses = useCallback(() => {
    if (!isInitialized || fixedRules.length === 0) return;

    const now = new Date();
    const startOfCurrentMonth = startOfMonth(now);

    // Verificar si ya se corrió este proceso este mes
    const alreadyGenerated = transactions.some(tx => 
        tx.isFixed && tx.fixedRuleId && isSameMonth(new Date(tx.date), now)
    );

    if (alreadyGenerated) return;

    console.log("Generando gastos fijos del mes...");
    const newTransactions: Transaction[] = fixedRules.map(rule => {
        // Crear la fecha para este mes en el día configurado
        const txDate = setDate(startOfCurrentMonth, rule.dayOfMonth);
        
        return {
            id: uuidv4(),
            type: 'expense',
            amount: 0, // Al crearse automáticamente, el valor REAL pagado es 0 hasta que el usuario lo actualice
            budgetedAmount: rule.budgetedAmount, // Este es el valor que se espera pagar
            accountId: rule.accountIdToCharge,
            categoryId: rule.categoryId,
            subCategoryId: rule.subCategoryId,
            description: `${rule.description} (Proyectado)`,
            date: txDate.toISOString(),
            isFixed: true,
            fixedRuleId: rule.id
        };
    });

    // Agregamos en lote y NO actualizamos saldos porque el amount real es 0.
    setTransactions(prev => [...newTransactions, ...prev]);

  }, [isInitialized, fixedRules, transactions]);

  // Ejecutar la generación de gastos fijos cuando carga la app y el contexto está listo
  useEffect(() => {
    if(isInitialized) {
        generateMonthlyFixedExpenses();
    }
  }, [isInitialized, generateMonthlyFixedExpenses]);


  // --- FUNCIONES CRUD (Simplificadas para el ejemplo, se pueden expandir) ---
  // NOTA IMPORTANTE: Para un sistema de producción real, editar una transacción vieja
  // debería recalcular todos los saldos desde esa fecha. Aquí usamos una aproximación simple.
  
  const updateTransaction = (updatedTx: Transaction) => {
    setTransactions(prev => prev.map(tx => tx.id === updatedTx.id ? updatedTx : tx));
    // NOTA: Aquí falta una lógica compleja para revertir el saldo anterior y aplicar el nuevo.
    // Para este prototipo, es mejor borrar y volver a crear si el monto cambia drásticamente.
    // Se implementará una versión simple: calcular la diferencia.
    const oldTx = transactions.find(t => t.id === updatedTx.id);
    if(!oldTx) return;

    const oldEffect = oldTx.type === 'income' ? oldTx.amount : -oldTx.amount;
    const newEffect = updatedTx.type === 'income' ? updatedTx.amount : -updatedTx.amount;
    
    if(oldTx.accountId === updatedTx.accountId) {
        // Misma cuenta, solo ajustar la diferencia
        updateAccountBalance(updatedTx.accountId, newEffect - oldEffect);
    } else {
        // Cambio de cuenta: revertir en la vieja, aplicar en la nueva
        updateAccountBalance(oldTx.accountId, -oldEffect);
        updateAccountBalance(updatedTx.accountId, newEffect);
    }

    // REVISAR LÓGICA DE AHORRO AL EDITAR (Si ahora sobra dinero y antes no)
    if (updatedTx.type === 'expense' && updatedTx.isFixed && updatedTx.budgetedAmount && !updatedTx.hasGeneratedSavings) {
         if(updatedTx.amount < updatedTx.budgetedAmount) {
             const savingsAmount = updatedTx.budgetedAmount - updatedTx.amount;
             const savingsTx: Transaction = {
                 id: uuidv4(), type: 'income', amount: savingsAmount,
                 accountId: 'acc_dav_th', categoryId: 'cat_sys_savings',
                 description: `Ahorro automático (corrección): ${updatedTx.description}`,
                 date: new Date().toISOString(), isFixed: false
             };
             setTransactions(prev => [savingsTx, ...prev]);
             updateAccountBalance(savingsTx.accountId, savingsTx.amount);
             // Marcar la original como que ya generó ahorro
             setTransactions(prev => prev.map(tx => tx.id === updatedTx.id ? {...tx, hasGeneratedSavings: true} : tx));
         }
    }
  };


  const deleteTransaction = (id: string) => {
    const txToDelete = transactions.find(tx => tx.id === id);
    if(!txToDelete) return;
    // Revertir saldo
    const multiplier = txToDelete.type === 'income' ? -1 : 1; // Inverso para borrar
    updateAccountBalance(txToDelete.accountId, txToDelete.amount * multiplier);
    setTransactions(prev => prev.filter(tx => tx.id !== id));
  };

  // Admin functions placeholders
  const addCategory = (c: Category) => setCategories(prev => [...prev, c]);
  const updateCategory = (c: Category) => setCategories(prev => prev.map(cat => cat.id === c.id ? c : cat));
  const addAccount = (a: Account) => setAccounts(prev => [...prev, a]);
  const updateAccount = (a: Account) => setAccounts(prev => prev.map(acc => acc.id === a.id ? a : acc));
  const addFixedRule = (r: FixedExpenseRule) => setFixedRules(prev => [...prev, r]);
  const updateFixedRule = (r: FixedExpenseRule) => setFixedRules(prev => prev.map(rule => rule.id === r.id ? r : rule));
  const deleteFixedRule = (id: string) => setFixedRules(prev => prev.filter(rule => rule.id !== id));


  return (
    <FinanceContext.Provider value={{
      accounts, transactions, categories, fixedRules,
      addTransaction, updateTransaction, deleteTransaction,
      addCategory, updateCategory, addAccount, updateAccount,
      addFixedRule, updateFixedRule, deleteFixedRule
    }}>
      {isInitialized ? children : <div className="p-8 text-center">Cargando finanzas...</div>}
    </FinanceContext.Provider>
  );
};

export const useFinance = () => {
  const context = useContext(FinanceContext);
  if (!context) throw new Error("useFinance must be used within FinanceProvider");
  return context;
};